.directory-tile {
  position: relative;
  padding: 0px;
  border: 2px solid var(--text-color);
  overflow: hidden;
  margin: 0px auto 30px;
}

.directory-tile:hover {
  box-shadow: 0 1px 5px 1px var(--highlight-orange);
}

.directory-tile span {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  text-align: center;
  font-size: 1em;
  text-transform: capitalize;
  background-color: rgba(0,0,0,.5);
  color: var(--text-color);
  opacity: .8;
}

.DirectoryContainer {
  background-color: var(--primary);
  padding: 45px;
/*  margin-left: 0%;
  margin-right: 0%;*/
}

/* JUMBOTRON: Pic of me and introduction upon hover */
.container .jumbotron {
  margin-top: 15px;
  padding: 0px;
  width: 100%;
  position: relative;
  background-color: #fff;
}
.container .jumbotron p {
  position: absolute;
  text-align: center;
  right: auto;
  top: 25%;
  -webkit-text-stroke: 1px #111; /* text outline */
  margin-left: 5%;
  margin-right: 5%;
  visibility: hidden;
  font-weight: bold;
  letter-spacing: .02em;
}
.container #jumbotron-image {
  margin-top: 15px;
}
.container .jumbotron:hover #jumbotron-image {
  opacity: .5;
}
.container .jumbotron:hover p {
  visibility: visible;
}

/********** Large devices only **********/
@media (min-width: 1200px) {
  .container .jumbotron {
    height: 755px;  
  }
  .container .jumbotron p {
      font-size: 50px;
  }
}

/********** Medium devices only **********/
@media (min-width: 992px) and (max-width: 1199px) {
  .container .jumbotron {
    height: 635px;  
  }
  .container .jumbotron p {
    font-size: 40px;
  } 
}

/********** Small devices only **********/
@media (min-width: 768px) and (max-width: 991px) {
