  color: #000;
  text-decoration: none;
  width: 100%;
}
.dropdown-item.active, .dropdown-item:active {
  background-color: var(--primary);
}
.navbar-brand a:hover, .navbar-brand a:focus {
  text-decoration: none;
}

/* Nav link items */
.navbar-nav .nav-link {
  color: var(--text-color);
  text-align: center;
  padding-top: 15px;
}

/* Collapsable button */
