.navbar {
  background-color: var(--primary);
}

#collapsable-nav {
  text-align: center;
}

/* Top nav bar */
.navbar-brand {
  cursor: pointer;
  padding-top: 15px;
}

.navbar-brand a {
  color: #fff;
  text-decoration: none;
}

/* Location dropdown menu */
.dropdown-item a {
  color: #000;
  text-decoration: none;
  width: 100%;
}
.dropdown-item.active, .dropdown-item:active {
  background-color: var(--primary);
}
.navbar-brand a:hover, .navbar-brand a:focus {
  text-decoration: none;
}

/* Nav link items */
.navbar-nav .nav-link {
  color: var(--text-color);
  text-align: center;
  padding-top: 15px;
}

/* Collapsable button */
.navbar-toggler-icon {
  width: 1em;
  height: 1em;
}

/********** Large devices only **********/
@media (min-width: 1200px) {

}

/********** Medium devices only **********/
@media (min-width: 992px) and (max-width: 1199px) {

}

/********** Small devices only **********/
@media (min-width: 768px) and (max-width: 991px) {
  .navbar-nav .nav-link {
    font-size: 12px;
  }
}

/********** Extra small devices only **********/
@media (max-width: 767px) {
  .dropdown-item {
    text-align: center;
  }
}


/********** Super extra small devices Only :-) (e.g., iPhone 4) **********/
@media (max-width: 479px) {
  .navbar-brand {
    font-size: 1em;
  }

  .col-xxs-12 {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    float: left;
    width: 100%;
  }
}